"use client";

import SidebarHeader from "./SidebarHeader";
import SidebarNav from "./SidebarNav";
import SidebarFooter from "./SidebarFooter";

type SidebarProps = {
  collapsed: boolean;
  setCollapsed: React.Dispatch<React.SetStateAction<boolean>>;
  activeKey: string;
  setActiveKey: React.Dispatch<React.SetStateAction<string>>;
};

export default function Sidebar({
  collapsed,
  setCollapsed,
  activeKey,
  setActiveKey,
}: SidebarProps) {
  return (
    <aside
      className={`flex flex-col justify-between h-screen bg-gray-100 border-r border-gray-300 transition-all duration-300 ${
        collapsed ? "w-16" : "w-60"
      }`}
    >
      <SidebarHeader collapsed={collapsed} setCollapsed={setCollapsed} />
      <SidebarNav collapsed={collapsed} activeKey={activeKey} setActiveKey={setActiveKey} />
      <SidebarFooter collapsed={collapsed} activeKey={activeKey} setActiveKey={setActiveKey} />
    </aside>
  );
}
